<template>
  <div class="login">

    <transition name="el-fade-in-linear">
      <div v-show="show" class="box transition-box">
        <h2>登录</h2>
        <el-input
          type="text"
          placeholder="请输入内容"
          v-model="form.username"
          axlength="10"
          class="input"
          show-word-limit
        ></el-input>
        <el-input
          placeholder="请输入密码"
          v-model="form.password"
          show-password
          class="input"
        ></el-input>
        <el-button class="button" type="primary" @click="login" plain
          >登录</el-button
        >
        <router-link to="#" class="forget">忘记密码</router-link>
      </div>

    </transition>

  </div>
</template>

<script>
import { reqLogin } from "../../util/request";
import { successAlert, warningAlert } from "../../util/alert";
import { mapGetters, mapActions } from "vuex";

export default {
  computed: {
    ...mapGetters({
      gettersuser: "gettersuser",
    }),
  },

  data() {
    return {
      form: {
        username: "admin",
        password: "123",
      },
    show:false

    };
  },

  methods: {
    ...mapActions({
      actionsuser: "actionsuser",
    }),

    login() {
      reqLogin(this.form).then((res) => {
        if (res.data.code == 200) {
          // 存储在状态层 一份数据 存储在 本地缓存一份
          // 保存到状态层一份
          this.actionsuser(res.data.list);
          this.$router.push("/");

          successAlert(res.data.msg);
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  mounted(){
    setTimeout(() => {
        this.show = true
    }, 300);
  }
};
</script>

<style scoped>
.login {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(
    -225deg,
    #2cd8d5 0%,
    #c5c1ff 56%,
    #ffbac3 100%
  );
}
.box {
  width: 500px;
  height: 320px;
  background-color: #fff;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 50px;
  transform: translate(-50%, -50%);
}

h2 {
  text-align: center;
  margin-bottom: 10px;
}

.input {
  height: 50px;
  margin-bottom: 20px;
}
.button {
  width: 100%;
}
.forget {
  width: 100%;
  display: inline-block;
  font-size: 14px;
  color: #006664;
  margin-top: 10px;
  text-align: center;
}
</style>


